/*--------------------------------------
    Base
--------------------------------------*/
body {
  color: #666;
}
h1 {
  margin: 0;
  color: #666;
  font-weight: bold;
}
ul {
  margin-bottom: 0;
}
li {
  list-style: none;
}
dd {
  margin: 0;
}
dl {
  margin: 0;
}
table,
p {
  font-size: 13px;
}

/* Frame
–––––––––––––––––––––––––––––––––––––––––––––––––– */
#container {
  position: relative;
}
#header {
  width: 100%;
  min-width: 1040px;
  background: #f2f2f2;
  border-bottom: 2px solid #b2b2b2;
}
.column-left {
  float: left;
  position: relative;
}
.column-right {
  float: right;
}
#contents {
  min-width: 960px;
}
#contents #sidebar {
  width: 220px;
  position: relative;
}
#contents #main {
  margin-top: 20px;
  margin-left: 0px;
  margin-right: 20px;
}
#footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  min-width: 960px;
  height: 40px;
  background: #f2f2f2;
  z-index: 9999;
  border-top: 2px solid #b2b2b2;
}

/*--------------------------------------
    Header
--------------------------------------*/
#header .column-left {
  text-align: left;
  height: 45px;
  padding: 17px 15px 15px 15px;
  padding-right: 0;
  vertical-align: middle;
  display: table-cell;
}
#header .column-left .hd-logo img {
  width: 100%;
  padding: 6px 0 0;
}
#header .column-right {
  width: 45%;
  text-align: right;
  height: 45px;
  padding: 17px 15px 15px 15px;
  padding-left: 0;
}
#header .column-right .submenu li {
  display: inline-block;
}
#header .column-right .submenu li:last-child {
  margin-left: 10px;
}
#header .column-right .submenu li:first-child {
  margin-left: 0;
}
#header .column-right .name {
  font-size: 100%;
}
#header .column-right .last-time {
  font-size: 80%;
}

/*--------------------------------------
    Footer
--------------------------------------*/
#footer .floatleft {
  margin-left: 15px;
}
#footer .floatright {
  margin-right: 15px;
}
#footer .logo {
  line-height: 40px;
  margin-right: 10px;
}
#footer .logo img {
  vertical-align: middle;
}
#footer small {
  font-size: 13px;
  line-height: 40px;
  font-family: Arial;
}
#footer .logo-img {
  margin: 5px 15px 5px 0;
}
#footer .submenu li {
  margin-left: 15px;
  list-style: none;
  line-height: 40px;
  display: inline-block;
}
#footer .submenu li:before {
  content: '▶';
  padding-right: 4px;
  font-size: 40%;
}
#footer .submenu li a {
  font-size: 12px;
  color: #666;
  text-decoration: none;
}
#page-top {
  position: fixed;
  bottom: 60px;
  right: 32px;
  font-size: 77%;
  cursor: pointer;
}
#page-top a {
  display: table-cell;
  vertical-align: middle;
  text-decoration: none;
  color: #fff;
  background: rgba(169, 203, 2, 0.8)
    url(https://stara.bizconcier-dm.com/wide_img/icon-arrow-pagetop.png) no-repeat 5px bottom;
  padding: 10px 0 10px 40px;
  font-size: 13px;
  text-align: center;
  display: block;
  border-radius: 2px;
  letter-spacing: 0.1em;
  font-size: 13px;
  width: 80px;
  text-align: left;
}
.system-version {
  color: #999;
}

/* Sidebar
–––––––––––––––––––––––––––––––––––––––––––––––––– */
#sidebar .bg {
  position: absolute;
  top: 0;
  left: 0;
  border-right: 2px solid #b2b2b2;
  width: 218px;
  height: 100%;
  min-height: 900px;
  z-index: 0;
}
#sidebar .menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 220px;
}
#sidebar ul {
  display: none;
  color: #7f7f7f;
  background: #fff;
  margin-bottom: -5px;
  padding-bottom: 15px;
}
#sidebar li {
  list-style: none;
}
#sidebar li span {
  cursor: pointer;
  padding-left: 20px;
}
#sidebar ul > li {
  margin-bottom: 5px;
  font-size: 13px;
}
#sidebar ul li ul li {
  background: #ccc;
  margin: 0 10px 0 20px;
  background: #dcea9a;
  text-align: right;
  padding: 5px 10px;
  line-height: 1.2em;
}
#sidebar ul li ul li.on {
  background: #edf4cc;
}
#sidebar ul li ul li a ul li span {
  display: block;
  color: #808080;
  text-decoration: none;
  font-size: 13px;
  letter-spacing: 0.01;
}
#sidebar .category-top-link {
  cursor: pointer;
  display: block;
  line-height: 60px;
  border-top: 2px solid #b2b2b2;
  border-right: 2px solid #b2b2b2;
  background: #fff;
  background-position: 14px center;
  background-repeat: no-repeat;
  position: relative;
  z-index: 9999;
  transition-duration: 0.2s;
  padding-left: 55px;
}
#sidebar .category-top {
  display: block;
  line-height: 60px;
  border-top: 2px solid #b2b2b2;
  border-right: 2px solid #b2b2b2;
  background: #f2f2f2;
  background-position: 14px center;
  background-repeat: no-repeat;
  position: relative;
  z-index: 0;
  transition-duration: 0.2s;
}
#sidebar .category-top > a {
  padding-left: 55px;
}

#sidebar .category-top > span {
  padding-left: 55px;
}

#sidebar .on.icon-arrow-right:after {
  margin: -3px 0 0 0;
  border-top: solid 2px #7f7f7f;
  border-right: solid 2px #7f7f7f;
  -webkit-transform: rotate(315deg);
  transform: rotate(315deg);
}
#sidebar span.icon-arrow-right:after {
  display: block;
  content: '';
  position: absolute;
  top: 50%;
  right: 15px;
  width: 8px;
  height: 8px;
  margin: -8px 0 0 0;
  border-top: solid 2px #7f7f7f;
  border-right: solid 2px #7f7f7f;
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
}
#sidebar span a {
  cursor: pointer;
  display: block;
  color: #808080;
  text-decoration: none;
}

#sidebar span span {
  cursor: pointer;
  display: block;
  color: #808080;
  text-decoration: none;
}

#sidebar .on span span {
  color: #fff;
}

#sidebar .on span a {
  color: #fff;
}
#sidebar .subcategory-top,
#sidebar .link {
  line-height: 1em;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 0 4px 4px 0;
  background: #ccc;
  display: block;
  position: relative;
}
#sidebar .on.category-top {
  color: #a9cb02;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  background-color: #fff;
}
#sidebar .on .subcategory-top:after {
  margin: -3px 0 0 0;
  border-top: solid 2px #fff;
  border-right: solid 2px #fff;
  -webkit-transform: rotate(315deg);
  transform: rotate(315deg);
}
#sidebar .on-other .subcategory-top:after {
  margin: -3px 0 0 0;
  -webkit-transform: rotate(315deg);
  transform: rotate(315deg);
}
#sidebar .on .subcategory-top {
  background: #a9cb02;
  color: #fff;
}
#sidebar .on-other .subcategory-top {
}
#sidebar .sidebar-menu-home {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-home-off.png);
  border-top: none;
}
#sidebar .sidebar-menu-home.on {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-home-on.png);
}
#sidebar .sidebar-menu-device {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-device-off.png);
}
#sidebar .sidebar-menu-device.on {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-device-on.png);
}
#sidebar .sidebar-menu-profile {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-profile-off.png);
}
#sidebar .sidebar-menu-profile.on {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-profile-on.png);
}

#sidebar .sidebar-menu-group {
  background-image: url('./images/group_off.png');
  background-size: 30px 30px;
}
#sidebar .sidebar-menu-group.on {
  background-image: url('./images/group_on.png');
  background-size: 30px 30px;
}

#sidebar .sidebar-menu-apps {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-apps-off.png);
}
#sidebar .sidebar-menu-apps.on {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-apps-on.png);
}
#sidebar .sidebar-menu-operation {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-operation-off.png);
}
#sidebar .sidebar-menu-operation.on {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-operation-on.png);
}
#sidebar .sidebar-menu-agreement {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-agreement-off.png);
  margin-top: -2px;
}
#sidebar .sidebar-menu-agreement.on {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-agreement-on.png);
  margin-top: -2px;
}
#sidebar .sidebar-menu-id {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-id-off.png);
}
#sidebar .sidebar-menu-id.on {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-id-on.png);
}
#sidebar .sidebar-menu-entry {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-entry-off.png);
}
#sidebar .sidebar-menu-entry.on {
  background-image: url(https://stara.bizconcier-dm.com/wide_img/sidebar-menu-entry-on.png);
