/* Others
–––––––––––––––––––––––––––––––––––––––––––––––––– */

/*--------------------------------------
    Contents
--------------------------------------*/
.container {
  min-width: 800px;
}
.contents-headder {
  margin-bottom: 15px;
  width: 800px;
  box-sizing: border-box;
}
.contents-headder .contents-title {
  height: 68px;
  border: 4px solid #d4e580;
  text-align: center;
  font-weight: bold;
  float: left;
  margin-right: 20px;
}
.contents-headder .contents-title h1 {
  width: 200px;
  height: 60px;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  line-height: 1.2em;
  padding: 2px 10px 0;
  font-size: 16px;
}
.contents-headder .contents-description {
  display: table-cell;
  vertical-align: middle;
  height: 60px;
  border: 4px solid #fff;
  text-align: left;
  font-size: 13px;
  float: left;
  width: 550px;
}
.contents-headder .contents-description div {
  height: 60px;
  display: table-cell;
  vertical-align: middle;
}
.contents-main {
  width: 800px;
  box-sizing: border-box;
}
#login {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -57%);
}
#login .logo {
  margin-bottom: 15px;
}
#login .text {
  font-size: 16px;
  text-align: center;
  line-height: 1.5em;
  margin-bottom: 0;
}
#login .table-cell {
  display: table-cell;
  vertical-align: middle;
  height: 330px;
}
#login .form {
  background: #e5e5e5;
  width: 320px;
  padding: 20px;
  margin-bottom: 15px;
}
#login .form input {
  background: #fff;
  width: 100%;
}
#login .form dl {
  margin-bottom: 20px;
}
#login .form dl dt {
  font-weight: bold;
  font-size: 14px;
  width: 100%;
  font-weight: lighter;
  margin-bottom: 4px;
}
#login .form dl dd {
  margin: 0 0 15px 0;
  width: 100%;
}
#login .form .btn-login {
  width: 260px;
  margin: 0 auto 20px;
  display: block;
}
#login .form .btn-login:hover {
  opacity: 0.95;
}
#login .form ul {
  margin-bottom: 0;
}
#login .form li {
  list-style: none;
}
#login .form li:before {
  content: '▶';
  padding-right: 4px;
  font-size: 40%;
}
#login .form li a {
  font-size: 13px;
  color: #666;
  text-decoration: none;
}
#login .form li a:hover {
  text-decoration: underline;
}
#contents .column-left {
  width: 230px;
  margin-right: -230px;
}
#contents .column-right {
  width: 100%;
  padding-bottom: 150px;
}
#contents .contents-main-border {
  border: 1px solid #b2b2b2;
  padding: 15px;
  margin-bottom: 15px;
  box-sizing: border-box;
}
#contents .required {
  color: #c74f4f;
  vertical-align: super;
}
#contents .table-header div {
  display: inline-block;
}
#contents .table-header span {
  font-size: 13px;
  line-height: 30px;
}
#contents .devicelist-searcharea {
  border: 1px solid #b2b2b2;
  padding: 15px;
}
#contents .devicelist-searcharea .first-line {
  padding-bottom: 15px;
}
#contents .devicelist-searcharea .second-line {
  border-bottom: 1px dotted #b2b2b2;
  padding: 0 0 15px;
}
#contents .devicelist-searcharea .third-line {
  padding-top: 15px;
}
#contents .devicelist-searcharea input[type='button'] {
  font-weight: lighter;
}
#contents .devicelist-searcharea input[type='button'].active {
  background: #a9cb02;
  color: #fff;
  border-color: #a9cb02;
}
#contents .table-devicelist .status {
  text-align: center;
  min-width: 50px;
}
#contents .table-devicelist th:not(.status) {
  min-width: 110px;
}
#contents .table-devicelist th a {
  color: #7f7f7f;
  text-decoration: none;
}
#contents .table-devicelist .arrow {
  position: relative;
  display: inline-block;
  padding: 0 0 0 16px;
  color: #000;
  vertical-align: middle;
  text-decoration: none;
  font-size: 15px;
  margin-top: -3px;
}
#contents .table-devicelist .arrow::before,
#contents .table-devicelist .arrow::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  content: '';
  vertical-align: middle;
}
#contents .table-devicelist .arrow-square.up::before {
  width: 12px;
  height: 12px;
  -webkit-border-radius: 25%;
  border-radius: 25%;
  background: #a9cb02;
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}
#contents .table-devicelist .arrow-square.up::after {
  top: -3.5px;
  left: 3px;
  box-sizing: border-box;
  width: 3px;
  height: 3px;
  border: 3px solid transparent;
  border-left: 3px solid #fff;
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}
#contents .table-devicelist .arrow-square.down::before {
  width: 12px;
  height: 12px;
  -webkit-border-radius: 25%;
  border-radius: 25%;
  background: #a9cb02;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
#contents .table-devicelist .arrow-square.down::after {
  top: 3px;
  right: 2.5px;
  box-sizing: border-box;
  width: 3px;
  height: 3px;
  border: 3px solid transparent;
  border-left: 3px solid #fff;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
#contents .dashboard .topics dl {
  margin-bottom: 10px;
  position: relative;
}
#contents .dashboard .topics dt,
#contents .dashboard .topics dd {
  font-size: 13px;
  line-height: 1.2em;
}
#contents .dashboard .topics dt {
  position: absolute;
  width: 90px;
  top: 0;
  left: 0;
}
#contents .dashboard .topics dd {
  margin-left: 90px;
}
#contents .dashboard .topics .new {
  color: #bd6363;
  margin-left: 5px;
}
#contents .dashboard .otherstopics li {
  font-size: 13px;
  line-height: 1em;
}
#contents .dashboard .otherstopics li.red {
  color: #bd6363;
}
.situation ul.situation-tab {
  padding: 0;
  height: 35px;
  margin-bottom: 0;
  position: relative;
  border-bottom: 4px solid #a9cb02;
}
.situation ul.situation-tab li {
  list-style-type: none;
  height: 34px;
  float: left;
  font-size: 12px;
}
.situation ul.situation-tab li a {
  outline: none;
  display: block;
  line-height: 34px;
  text-align: center;
  border-radius: 6px 6px 0 0;
  background: #fff;
  border: 1px solid #b2b2b2;
  border-bottom: none;
  text-decoration: none;
  color: #666;
}
.situation ul.situation-tab li a.selected {
  text-decoration: none;
  color: #fff;
  cursor: default;
  background: #a9cb02;
}
.situation .situation-panel {
  clear: both;
  border: 1px solid #b2b2b2;
  border-top: none;
  padding: 15px;
  position: relative;
}
.situation .situation-panel input.btn-update {
  position: absolute;
  right: 15px;
  top: 15px;
  background: #a9cb02;
  color: #fff;
  border: 1px solid #a9cb02;
}
.device ul.device-tab {
  padding: 0;
  height: 35px;
  margin-bottom: 0;
  position: relative;
  border-bottom: 4px solid #a9cb02;
}
.device ul.device-tab li {
  list-style-type: none;
  height: 34px;
  float: left;
  font-size: 12px;
}
.device ul.device-tab li a {
  outline: none;
  display: block;
  line-height: 34px;
  text-align: center;
  border-radius: 6px 6px 0 0;
  background: #fff;
  border: 1px solid #b2b2b2;
  border-bottom: none;
  text-decoration: none;
  color: #666;
}
.device ul.device-tab li a.selected {
  text-decoration: none;
  color: #fff;
  cursor: default;
  background: #a9cb02;
}
.device ul.device-panel {
  clear: both;
  border: 1px solid #b2b2b2;
  border-top: none;
  padding: 0;
}
.device ul.device-panel > li {
  list-style-type: none;
  padding: 10px;
  color: #333;
}
.navigation-bar {
  min-width: 690px;
}
.navigation-bar .navi {
  width: 100%;
  max-width: 240px;
  position: relative;
  float: left;
}
.navigation-bar span {
  display: inline-block;
  border: 1px solid #b2b2b2;
  box-sizing: border-box;
  padding: 8px 0 5px 0;
  text-align: center;
  width: 100%;
  border-radius: 3px;
}
.navigation-bar ul li.navi {
  display: table;
}
.navigation-bar ul li.navi span {
  height: 55px;
  display: table-cell;
  vertical-align: middle;
  line-height: 1.2em;
}
.navigation-bar .navigation-bar-arrow {
  width: 100%;
  max-width: 40px;
  height: 55px;
  position: relative;
  float: left;
}
.navigation-bar .navigation-bar-arrow::after {
  content: '▶';
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -8px;
  margin-left: -7px;
  color: #b2b2b2;
}
.navigation-bar li:last-child {
  padding-right: 0;
}
.navigation-bar li:last-child span::after {
  display: none;
}
.navigation-bar li.selected span {
  background: #dcea9a;
  border: 1px solid #dcea9a;
}
.profile .navigation-bar .navi {
  width: calc((100% / 9) - 10px);
  position: relative;
  float: left;
}
.profile .navigation-bar .navigation-bar-arrow {
  max-width: 25px;
  width: 3%;
  height: 55px;
  position: relative;
  float: left;
}
