/* table-yoko */
.table-yoko {
  border-collapse: collapse;
  margin-right: 50px;
  width: 100%;
  box-sizing: border-box;
}
.table-yoko th,
.table-yoko td {
  padding: 10px;
  font-size: 13px;
  word-break: break-all;
}
.table-yoko th {
  background: #e5e5e5;
  border: 1px solid #afafaf;
  text-align: center;
  font-weight: bold;
  color: #7f7f7f;
  word-break: keep-all;
  min-width: 100px;
}
.table-yoko td {
  border: 1px solid #afafaf;
  word-wrap: break-word;
  word-break: break-all;
}
.table-yoko th a {
  color: #7f7f7f;
}
.table-yoko .arrow {
  position: relative;
  display: inline-block;
  padding: 0 0 0 16px;
  color: #000;
  vertical-align: middle;
  text-decoration: none;
  font-size: 15px;
  margin-top: -3px;
}
.table-yoko .arrow::before,
.table-yoko .arrow::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  content: '';
  vertical-align: middle;
}
.table-yoko .arrow-square.up::before {
  width: 12px;
  height: 12px;
  -webkit-border-radius: 25%;
  border-radius: 25%;
  background: #a9cb02;
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}
.table-yoko .arrow-square.up::after {
  top: -3.5px;
  left: 3px;
  box-sizing: border-box;
  width: 3px;
  height: 3px;
  border: 3px solid transparent;
  border-left: 3px solid #fff;
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}
.table-yoko .arrow-square.down::before {
  width: 12px;
  height: 12px;
  -webkit-border-radius: 25%;
  border-radius: 25%;
  background: #a9cb02;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.table-yoko .arrow-square.down::after {
  top: 3px;
  right: 4px;
  box-sizing: border-box;
  width: 3px;
  height: 3px;
  border: 3px solid transparent;
  border-left: 3px solid #fff;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
.table-yoko.center td {
  text-align: center;
}
/* テーブル内フォームパーツ */
.table-yoko label.radiobtn {
  margin-right: 0 !important;
  left: -8px;
}
.table-yoko label.radiobtn {
  padding: 0 !important;
}
.table-yoko label.radiobtn:before {
  top: 50%;
  margin-top: -10px;
  left: 50%;
  margin-left: -10px;
}
.table-yoko label.radiobtn:after {
  left: -4px !important;
}
