input[type='radio'] {
  position: relative;
  opacity: 0;
}
label.radiobtn {
  display: inline-block;
  position: relative;
  cursor: pointer;
  padding: 2px 0 0 24px;
  border-radius: 3px;
  color: #3e4956;
  font-size: 13px;
  text-align: left;
  vertical-align: middle;
  background: #fff;
  margin: 0 10px 0 0;
  color: #666;
  font-weight: lighter;
  left: -13px;
}
label.radiobtn:last-child {
  margin-bottom: 0;
}
label.radiobtn:before {
  position: absolute;
  content: '';
  top: 50%;
  left: 0;
  width: 16px;
  height: 16px;
  margin-top: -10px;
  background: #fff;
  border: 2px solid #ccc;
  border-radius: 25%;
}
input[type='radio']:checked + label.radiobtn:before {
  background: #fff;
  border: 2px solid #a9cb02;
}
input[type='radio']:checked + label.radiobtn:after {
  position: absolute;
  content: '';
  top: 50%;
  left: 6px;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  border-radius: 50%;
  background: #a9cb02;
}
input[type='radio']:focus + label.radiobtn:before {
  outline-color: 2px -webkit-focus-ring-color;
  outline-style: auto;
  outline-width: 1px;
}
