  padding: 10px 0;
  margin-bottom: 5px;
  font-size: 13px;
  color: #666;
}
label.checkbox:last-child {
  margin-bottom: 0;
}
.checkbox-parts::before {
  content: '';
  display: block;
  position: absolute;
  top: -2px;
  left: 0;
  width: 15px;
  height: 15px;
  border: 2px solid #ccc;
  border-radius: 3px;
  background: #fff;
}
.checkbox-input:checked + .checkbox-parts::before {
  background: #a9cb02;
  border-color: #a9cb02;
}
.checkbox-input:checked + .checkbox-parts::after {
  content: '';
  display: block;
  position: absolute;
  top: -1px;
  left: 5px;
  width: 6px;
  height: 10px;
  transform: rotate(40deg);
  border-bottom: 3px solid #fff;
  border-right: 3px solid #fff;
}
input[type='checkbox']:focus + .checkbox-parts:before {
  outline-color: 2px -webkit-focus-ring-color;
  outline-style: auto;
  outline-width: 1px;
}

.select-group {
  position: relative;
  display: inline-block;
  overflow: hidden;
  box-sizing: border-box;
  padding: 0;
  vertical-align: middle;
  border: 1px solid #b5b5b5;
  border-radius: 4px;
  background-color: #fff;
  background-repeat: no-repeat;
